.messageContainer {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.message {
    margin: 5px;
}

.messageBox {
    display: flex;
    align-items: center;
    margin: 10px;
    padding: 10px;
    border: 1px solid #D3EEE5;
    border-radius: 15px;
    width: auto;
    background-color: #83c5be;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    margin: 10px;
}
